/* 1. Global - Set up the things
/* ---------------------------------------------------------- */
@import "global.css";

body {
  background: #f4f8fb;
}

.img {
  display: block;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center center;
  border-radius: 100%;
}

.hidden {
  visibility: hidden;
  position: absolute;
  text-indent: -9999px;
}

/* 2. Layout - Page building blocks
/* ---------------------------------------------------------- */

.site-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.site-main {
  z-index: 100;
  flex-grow: 1;
}

/* Full width page blocks */
.outer {
  position: relative;
  padding: 0 4vw;
}

/* Centered content container blocks */
.inner {
  width: 100%;
  max-width: 1040px;
  margin: 0 auto;
}

/* Usage:

<div class="outer">
    <div class="inner">
        Centered content
    </div>
</div>

*/

/* 3. Special Template Styles
/* ---------------------------------------------------------- */

@media (min-width: 900px) {
  .home-template .post-feed,
  .tag-template .post-feed,
  .author-template .post-feed {
    margin-top: -70px;
    padding-top: 0;
  }

  .home-template .site-nav {
    position: relative;
    top: -70px;
  }
}

/* 4. Site Header
/* ---------------------------------------------------------- */

.site-header {
  position: relative;
  padding-top: 12px;
  padding-bottom: 12px;
  color: #fff;
  background: color(var(--darkgrey) l(-5%)) no-repeat center center;
  background-size: cover;
}

.site-header::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10;
  display: block;
  background: rgba(0, 0, 0, 0.18);
}

.site-header::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: auto;
  left: 0;
  z-index: 10;
  display: block;
  height: 80px;
  background: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0));
}

.site-header.no-cover::before,
.site-header.no-cover::after {
  display: none;
}

.site-header-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 200px;
  max-height: 450px;
  padding: 10vw 4vw;
  text-align: center;
}

.site-title {
  z-index: 10;
  margin: 0;
  padding: 0;
  font-size: 3.8rem;
  font-weight: 700;
}

.site-logo {
  max-height: 45px;
}

.site-description {
  z-index: 10;
  margin: 0;
  padding: 5px 0;
  font-size: 2.2rem;
  font-weight: 300;
  letter-spacing: 0.5px;
  opacity: 0.8;
}

@media (max-width: 500px) {
  .site-title {
    font-size: 3rem;
  }

  .site-description {
    font-size: 1.8rem;
  }
}

/* 5. Site Navigation
/* ---------------------------------------------------------- */

.site-nav {
  position: relative;
  z-index: 300;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  height: 40px;
  overflow-y: hidden;
  font-size: 1.2rem;
}

.site-nav-left {
  display: flex;
  align-items: center;
  overflow-x: auto;
  overflow-y: hidden;
  margin-right: 10px;
  padding-bottom: 80px;
  letter-spacing: 0.4px;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-scrolling: touch;
}

/* Site Nav Hack Explanation (above):

What's happening above it .site-nav-left is set to overflow-x and allow sideways scrolling, so that when there isn't enough space for all nav items (either due to lots of nav items, or a small viewport), you can still scroll side-to-side to reach them.

The knock-on effect of this is ugly browser-scroll bars at the bottom, so 80px of padding-bottom and a 40px fixed height parent (.site-nav) hides that entirely. Slightly hacky code. But nice clean end-result.

*/

.site-nav-logo {
  flex-shrink: 0;
  display: block;
  margin-right: 24px;
  padding: 11px 0;
  color: #fff;
  font-size: 1.7rem;
  line-height: 1em;
  font-weight: bold;
  letter-spacing: -0.5px;
}

.site-nav-logo:hover {
  text-decoration: none;
}

.site-nav-logo img {
  display: block;
  width: auto;
  height: 21px;
}

.nav {
  display: flex;
  margin: 0 0 0 -12px;
  padding: 0;
  list-style: none;
}

.nav li {
  display: block;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
}

.nav li a {
  display: block;
  margin: 0;
  padding: 10px 12px;
  color: #fff;
  opacity: 0.8;
}

.nav li a:hover {
  text-decoration: none;
  opacity: 1;
}

.site-nav-right {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  height: 40px;
}

.social-links {
  flex-shrink: 0;
  display: flex;
  align-items: center;
}

.social-links a:last-of-type {
  padding-right: 20px;
}

.social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 10px;
  color: #fff;
  opacity: 0.8;
}

.social-link:hover {
  opacity: 1;
}

.social-link svg {
  height: 1.8rem;
  fill: #fff;
}

.social-link-fb svg {
  height: 1.5rem;
}

.social-link-wb svg {
  height: 1.6rem;
}

.social-link-wb svg path {
  stroke: #fff;
}

.social-link-rss svg {
  height: 1.9rem;
}

.subscribe-button {
  display: block;
  padding: 4px 10px;
  border: #fff 1px solid;
  color: #fff;
  font-size: 1.2rem;
  line-height: 1em;
  border-radius: 10px;
  opacity: 0.8;
}

.subscribe-button:hover {
  text-decoration: none;
  opacity: 1;
}

.rss-button {
  opacity: 0.8;
}

.rss-button:hover {
  opacity: 1;
}

.rss-button svg {
  height: 2.1rem;
  margin-bottom: 1px;
  fill: #fff;
}

@media (max-width: 700px) {
  .site-header {
    padding-right: 0;
    padding-left: 0;
  }

  .site-nav-left {
    margin-right: 0;
    padding-left: 4vw;
  }

  .site-nav-right {
    display: none;
  }
}

/* 6. Post Feed
/* ---------------------------------------------------------- */

.post-feed {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin: 0 -20px;
  padding: 40px 0 0;
}

.post-card {
  flex: 1 1 300px;
  display: flex;
  flex-direction: column;
  min-height: 300px;
  overflow: hidden;
  margin: 0 20px 40px;
  background: #fff center center;
  background-size: cover;
  border-radius: 5px;
  box-shadow: rgba(39, 44, 49, 0.06) 8px 14px 38px, rgba(39, 44, 49, 0.03) 1px 3px 8px;
  transition: all 0.5s ease;
}

.post-card:hover {
  box-shadow: 0 0 1px rgba(39, 44, 49, 0.1), 0 3px 16px rgba(39, 44, 49, 0.07);
  transition: all 0.3s ease;
  transform: translate3d(0, -1px, 0);
}

.post-card-image-link {
  position: relative;
  display: block;
  overflow: hidden;
  border-radius: 5px 5px 0 0;
}

.post-card-image {
  width: auto;
  height: 200px;
  background: var(--lightgrey) no-repeat center center;
  background-size: cover;
}

.post-card-content-link {
  position: relative;
  display: block;
  padding: 25px 25px 0;
  color: var(--darkgrey);
}

.post-card-content-link:hover {
  text-decoration: none;
}

.post-card-tags {
  display: block;
  margin-bottom: 4px;
  color: var(--midgrey);
  font-size: 1.2rem;
  line-height: 1.15em;
  font-weight: 500;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

.post-card-title {
  margin-top: 0;
}

.post-card-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.post-card-excerpt {
  font-family: Georgia, serif;
}

.post-card-meta {
  padding: 0 25px 25px;
}

.author-profile-image {
  width: 25px;
  height: 25px;
  margin-right: 5px;
  border-radius: 100%;
  object-fit: cover;
}

.post-card-author {
  font-size: 1.3rem;
  font-weight: 500;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

/* Special Styling for home page grid (below):

The first (most recent) post in the list is styled to be bigger than the others and take over the full width of the grid to give it more emphasis. Wrapped in a media query to make sure this only happens on large viewports / desktop-ish devices.

 */

@media (min-width: 795px) {
  .home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) {
    flex: 1 1 100%;
    flex-direction: row;
  }

  .home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) .post-card-image-link {
    position: relative;
    flex: 1 1 auto;
  }

  .home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) .post-card-image {
    position: absolute;
    width: 100%;
    height: 100%;
  }

  .home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) .post-card-content {
    flex: 0 1 357px;
  }

  .home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) h2 {
    font-size: 2.6rem;
  }

  .home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) p {
    font-size: 1.8rem;
    line-height: 1.55em;
  }

  .home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) .post-card-content-link {
    padding: 30px 40px 0;
  }

  .home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) .post-card-meta {
    padding: 0 40px 30px;
  }
}

.home-template .site-header::after {
  display: none;
}

/* Adjust some margins for smaller screens */
@media (max-width: 650px) {
  .post-feed {
    padding-top: 5vw;
  }

  .post-card {
    margin: 0 20px 5vw;
  }
}

/* 7. Single Post
/* ---------------------------------------------------------- */

.post-template .site-main,
.page-template .site-main {
  padding-bottom: 4vw;
  background: #fff;
}

.post-full {
  position: relative;
  z-index: 50;
}

/* ^ Required to make .post-full-content:before/after z-index stacking work */

.post-full-header {
  max-width: 1040px;
  margin: 0 auto;
  padding: 6vw 3vw 3vw;
  text-align: center;
}

@media (max-width: 500px) {
  .post-full-header {
    padding: 14vw 3vw 10vw;
  }
}

.post-full-meta {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--midgrey);
  font-size: 1.4rem;
  font-weight: 600;
  text-transform: uppercase;
}

.post-full-meta-date {
  color: var(--blue);
}

.post-full-title {
  margin: 0;
  color: color(var(--darkgrey) l(-5%));
}

.date-divider {
  display: inline-block;
  margin: 0 6px 1px;
}

.post-full-image {
  height: 800px;
  margin: 0 -10vw -165px;
  background: var(--lightgrey) center center;
  background-size: cover;
  border-radius: 5px;
}

@media (max-width: 1170px) {
  .post-full-image {
    height: 600px;
    margin: 0 -4vw -100px;
    border-radius: 0;
  }
}

@media (max-width: 800px) {
  .post-full-image {
    height: 400px;
  }
}

.post-full-content {
  position: relative;
  min-height: 230px;
  margin: 0 auto;
  padding: 70px 100px 0;
  font-family: Georgia, serif;
  font-size: 2.2rem;
  line-height: 1.6em;
  background: #fff;
}

@media (max-width: 1170px) {
  .post-full-content {
    padding: 5vw 7vw 0;
  }
}

@media (max-width: 800px) {
  .post-full-content {
    font-size: 1.9rem;
  }
}

.post-full-content::before {
  content: "";
  position: absolute;
  top: 15px;
  left: -5px;
  z-index: -1;
  display: block;
  width: 20px;
  height: 200px;
  background: rgba(39, 44, 49, 0.15);
  filter: blur(5px);
  transform: rotate(-5deg);
}

.post-full-content::after {
  content: "";
  position: absolute;
  top: 15px;
  right: -5px;
  z-index: -1;
  display: block;
  width: 20px;
  height: 200px;
  background: rgba(39, 44, 49, 0.15);
  filter: blur(5px);
  transform: rotate(5deg);
}

.no-image .post-full-content {
  padding-top: 0;
}

.no-image .post-full-content::before,
.no-image .post-full-content::after {
  display: none;
}

.kg-card-markdown {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 920px;
}

.post-full-content h1,
.post-full-content h2,
.post-full-content h3,
.post-full-content h4,
.post-full-content h5,
.post-full-content h6,
.post-full-content p,
.post-full-content ul,
.post-full-content ol,
.post-full-content dl,
.post-full-content pre,
.post-full-content blockquote,
.post-full-comments,
.footnotes {
  min-width: 100%;
}

.post-full-content li {
  word-break: break-word;
}

.post-full-content li p {
  margin: 0;
}

.post-template .kg-card-markdown > p:first-child {
  font-size: 1.25em;
  line-height: 1.5em;
}

.post-full-content a {
  color: #000;
  box-shadow: var(--blue) 0 -1px 0 inset;
}

.post-full-content a:hover {
  color: var(--blue);
  text-decoration: none;
}

.post-full-content strong,
.post-full-content em {
  color: color(var(--darkgrey) l(-5%));
}

.post-full-content small {
  display: inline-block;
  line-height: 1.6em;
}

.post-full-content li:first-child {
  margin-top: 0;
}

.post-full-content img,
.post-full-content video {
  display: block;
  max-width: 1040px;
  margin: 1.5em auto;
}

@media (max-width: 1040px) {
  .post-full-content img,
  .post-full-content video {
    width: 100%;
  }
}

/* Full bleed images (#full)
Super neat trick courtesy of @JoelDrapper

Usage (In Ghost edtior):

![img](/some/image.jpg#full)

*/
.post-full-content img[src$="#full"] {
  width: 100vw;
  max-width: none;
}

/* Image captions

Usage (In Ghost editor):

![img](/some/image.jpg)
<small>Your image caption</small>

*/
.post-full-content img + br + small {
  display: block;
  margin-top: -3em;
  margin-bottom: 1.5em;
}

.post-full-content iframe {
  margin: 0 auto;
}

.post-full-content blockquote {
  margin: 0 0 1.5em;
  padding: 1.5em 0;
  border-left: #3eb0ef 3px solid;
}

.post-full-content blockquote::before {
  content: open-quote;
}

.post-full-content blockquote p {
  margin: 0;
}

@media (min-width: 1000px) {
  .post-full-content blockquote:not(:first-of-type) {
    width: 100vw;
    max-width: 1060px;
  }
}

.post-full-content blockquote + blockquote {
  padding-top: 0;
}

.post-full-content code {
  padding: 0 5px 2px;
  font-size: 0.8em;
  line-height: 1em;
  font-weight: 400 !important;
  background: var(--whitegrey);
  border-radius: 3px;
}

.post-full-content pre {
  max-width: 100%;
  overflow-x: auto;
  margin: 1.5em 0 3em;
  padding: 20px;
  border: color(var(--darkgrey) l(-10%)) 1px solid;
  color: var(--whitegrey);
  font-size: 1.4rem;
  line-height: 1.5em;
  background: color(var(--darkgrey) l(-3%));
  border-radius: 5px;
}

.post-full-content pre code {
  padding: 0;
  font-size: inherit;
  line-height: inherit;
  background: transparent;
}

.post-full-content .fluid-width-video-wrapper {
  margin: 1.5em 0 3em;
}

.post-full-content hr {
  margin: 4vw 0;
}

.post-full-content hr::after {
  content: "";
  position: absolute;
  top: -15px;
  left: 50%;
  display: block;
  width: 1px;
  height: 30px;
  margin-left: -10px;
  background: color(var(--lightgrey) l(+10%));
  box-shadow: #fff 0 0 0 5px;
  transform: rotate(45deg);
}

.post-full-content h1,
.post-full-content h2,
.post-full-content h3,
.post-full-content h4,
.post-full-content h5,
.post-full-content h6 {
  color: color(var(--darkgrey) l(-5%));
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.post-full-content h1 {
  margin: 0.5em 0 0.2em;
  font-size: 4.6rem;
  font-weight: 700;
}

@media (max-width: 500px) {
  .post-full-content h1 {
    font-size: 2.8rem;
  }
}

.post-full-content h2 {
  margin: 0.5em 0 0.2em;
  font-size: 3.6rem;
  font-weight: 700;
}

@media (max-width: 500px) {
  .post-full-content h2 {
    font-size: 2.6rem;
  }
}

.post-full-content h3 {
  margin: 0.5em 0 0.2em;
  font-size: 2.8rem;
  font-weight: 700;
}

@media (max-width: 500px) {
  .post-full-content h3 {
    font-size: 2.2rem;
  }
}

.post-full-content h4 {
  margin: 0.5em 0 0.2em;
  font-size: 2.8rem;
  font-weight: 700;
}

@media (max-width: 500px) {
  .post-full-content h4 {
    font-size: 2.2rem;
  }
}

.post-full-content h5 {
  display: block;
  margin: 0.5em 0;
  padding: 1em 0 1.5em;
  border: 0;
  color: var(--blue);
  font-family: Georgia, serif;
  font-size: 3.2rem;
  line-height: 1.35em;
  text-align: center;
}

@media (min-width: 1180px) {
  .post-full-content h5 {
    width: 100vw;
    max-width: 1060px;
  }
}

@media (max-width: 500px) {
  .post-full-content h5 {
    padding: 0 0 0.5em;
    font-size: 2.2rem;
  }
}

.post-full-content h6 {
  color: color(var(--darkgrey) l(-5%));
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.post-full-content h6 {
  margin: 0.5em 0 0.2em;
  font-size: 2.3rem;
  font-weight: 700;
}

@media (max-width: 500px) {
  .post-full-content h6 {
    font-size: 2rem;
  }
}

.footnotes-sep {
  margin-bottom: 30px;
}

.footnotes {
  font-size: 1.5rem;
}

.footnotes p {
  margin: 0;
}

.footnote-backref {
  color: var(--blue) !important;
  font-size: 1.2rem;
  font-weight: bold;
  text-decoration: none !important;
  box-shadow: none !important;
}

/* Some grouped styles for smaller viewports */
@media (max-width: 500px) {
  .post-full-meta {
    font-size: 1.2rem;
    line-height: 1.3em;
  }

  .post-full-title {
    font-size: 2.9rem;
  }

  .post-full-image {
    height: 350px;
    margin-bottom: 4vw;
  }

  .post-full-content {
    padding: 0;
  }

  .post-full-content::before,
  .post-full-content::after {
    display: none;
  }
}

/* Tables */
.post-full-content table {
  display: inline-block;
  width: auto;
  max-width: 100%;
  overflow-x: auto;
  margin: 0.5em 0 2.5em;
  border-spacing: 0;
  border-collapse: collapse;
  vertical-align: top;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 1.6rem;
  white-space: nowrap;
}

.post-full-content table {
  background: radial-gradient(ellipse at left, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0) 75%) 0 center, radial-gradient(ellipse at right, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0) 75%) 100% center;
  background-size: 10px 100%, 10px 100%;
  background-attachment: scroll, scroll;
  background-repeat: no-repeat;
  -webkit-overflow-scrolling: touch;
}

.post-full-content table td:first-child {
  background-image: linear-gradient(to right, rgba(255, 255, 255, 1) 50%, rgba(255, 255, 255, 0) 100%);
  background-size: 20px 100%;
  background-repeat: no-repeat;
}

.post-full-content table td:last-child {
  background-image: linear-gradient(to left, rgba(255, 255, 255, 1) 50%, rgba(255, 255, 255, 0) 100%);
  background-size: 20px 100%;
  background-position: 100% 0;
  background-repeat: no-repeat;
}

.post-full-content table th {
  color: var(--darkgrey);
  font-size: 1.2rem;
  font-weight: 700;
  letter-spacing: 0.2px;
  text-align: left;
  text-transform: uppercase;
  background-color: color(var(--whitegrey) l(+4%));
}

.post-full-content table th,
.post-full-content table td {
  padding: 6px 12px;
  border: color(var(--whitegrey) l(-1%) s(-5%)) 1px solid;
}

/* 7.1. Subscribe Form
/* ---------------------------------------------------------- */

.subscribe-form {
  margin: 1.5em 0;
  padding: 6.5vw 7vw 7vw;
  border: color(var(--whitegrey) l(+2%)) 1px solid;
  text-align: center;
  background: color(var(--whitegrey) l(+4%));
  border-radius: 7px;
}

.subscribe-form-title {
  margin: 0 0 3px;
  padding: 0;
  color: var(--darkgrey);
  font-size: 3.5rem;
  line-height: 1;
  font-weight: 700;
}

.subscribe-form p {
  margin-bottom: 1em;
  color: var(--midgrey);
  font-size: 2.2rem;
  line-height: 1.55em;
  letter-spacing: 0.2px;
}

.subscribe-form form {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 420px;
  margin: 0 auto;
}

.subscribe-form .form-group {
  flex-grow: 1;
}

.subscribe-email {
  display: block;
  width: 100%;
  padding: 10px;
  border: color(var(--lightgrey) l(+7%)) 1px solid;
  color: var(--midgrey);
  font-size: 1.8rem;
  line-height: 1em;
  font-weight: normal;
  user-select: text;
  border-radius: 5px;
  transition: border-color 0.15s linear;
  -webkit-appearance: none;
}

.subscribe-email:focus {
  outline: 0;
  border-color: color(var(--lightgrey) l(-2%));
}

.subscribe-form button {
  display: inline-block;
  height: 41px;
  margin: 0 0 0 10px;
  padding: 0 20px;
  outline: none;
  color: #fff;
  font-size: 1.5rem;
  line-height: 37px;
  font-weight: 400;
  text-align: center;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.1);
  background: linear-gradient(color(var(--blue) whiteness(+7%)), color(var(--blue) lightness(-7%) saturation(-10%)) 60%, color(var(--blue) lightness(-7%) saturation(-10%)) 90%, color(var(--blue) lightness(-4%) saturation(-10%)));
  border-radius: 5px;
  box-shadow: 0 0 0 1px inset rgba(0, 0, 0, 0.14);
  -webkit-font-smoothing: subpixel-antialiased;
}

.subscribe-form button:active,
.subscribe-form button:focus {
  background: color(var(--blue) lightness(-9%) saturation(-10%));
}

@media (max-width: 650px) {
  .subscribe-form-title {
    font-size: 2.4rem;
  }

  .subscribe-form p {
    font-size: 1.6rem;
  }
}

@media (max-width: 500px) {
  .subscribe-form form {
    flex-direction: column;
  }

  .subscribe-form .form-group {
    width: 100%;
  }

  .subscribe-form button {
    width: 100%;
    margin: 10px 0 0;
  }
}

/* 7.2. Post Footer
/* ---------------------------------------------------------- */

.post-full-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 840px;
  margin: 0 auto;
  padding: 3vw 0 6vw;
}

.author-card {
  display: flex;
  align-items: center;
}

.author-card .author-profile-image {
  width: 60px;
  height: 60px;
  margin-right: 15px;
}

.author-card-name {
  margin: 0 0 2px;
  padding: 0;
  font-size: 2rem;
}

.author-card-name a {
  color: var(--darkgrey);
  font-weight: 700;
}

.author-card-name a:hover {
  text-decoration: none;
}

.author-card-content p {
  margin: 0;
  color: var(--midgrey);
  line-height: 1.3em;
}

.post-full-footer-right {
  flex-shrink: 0;
  margin-left: 20px;
}

.author-card-button {
  display: block;
  padding: 9px 16px;
  border: color(var(--midgrey) l(+20%)) 1px solid;
  color: var(--midgrey);
  font-size: 1.2rem;
  line-height: 1;
  font-weight: 500;
  border-radius: 20px;
  transition: all ease 0.2s;
}

.author-card-button:hover {
  border-color: var(--blue);
  color: var(--blue);
  text-decoration: none;
}

/* 7.3. Comments
/* ---------------------------------------------------------- */

.post-full-comments {
  max-width: 840px;
  margin: 0 auto;
}

/* 7.4. Related posts
/* ---------------------------------------------------------- */

.read-next-feed {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -20px;
  padding: 40px 0 0;
}

.read-next-card {
  position: relative;
  flex: 1 1 300px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  margin: 0 20px 40px;
  padding: 25px;
  color: #fff;
  background: var(--darkgrey) center center;
  background-size: cover;
  border-radius: 5px;
  box-shadow: rgba(39, 44, 49, 0.06) 8px 14px 38px, rgba(39, 44, 49, 0.03) 1px 3px 8px;
}

.read-next-card::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  background: linear-gradient(135deg, rgba(0, 40, 60, 0.8) 0%, rgba(0, 20, 40, 0.7) 100%);
  border-radius: 5px;
  backdrop-filter: blur(2px);
}

.read-next-card-header {
  position: relative;
  z-index: 50;
  padding-top: 20px;
  text-align: center;
}

.read-next-card-header-sitetitle {
  display: block;
  font-size: 1.3rem;
  line-height: 1.3em;
  opacity: 0.8;
}

.read-next-card-header-title {
  margin: 0;
  padding: 0 20px;
  color: #fff;
  font-size: 3rem;
  line-height: 1.2em;
  letter-spacing: 1px;
}

.read-next-card-header-title a {
  color: #fff;
  font-weight: 300;
  text-decoration: none;
}

.read-next-card-header-title a:hover {
  text-decoration: none;
}

.read-next-divider {
  position: relative;
  display: flex;
  justify-content: center;
  height: 80px;
}

.read-next-divider svg {
  width: 40px;
  stroke: #fff;
  fill: transparent;
  stroke-width: 0.5px;
  stroke-opacity: 0.65;
}

.read-next-card-content {
  position: relative;
  z-index: 50;
  flex-grow: 1;
  display: flex;
  font-size: 1.7rem;
}

.read-next-card-content ul {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0;
  list-style: none;
  text-align: center;
}

.read-next-card-content li {
  margin: 0;
  padding: 0;
  font-size: 1.6rem;
  line-height: 1.25em;
  font-weight: 200;
  letter-spacing: -0.5px;
}

.read-next-card-content li a {
  display: block;
  padding: 20px 0;
  border-bottom: rgba(255, 255, 255, 0.3) 1px solid;
  vertical-align: top;
  color: #fff;
  font-weight: 500;
  transition: opacity 0.3s ease;
}

.read-next-card-content li:first-of-type a {
  padding-top: 10px;
}

.read-next-card-content li a:hover {
  opacity: 1;
}

.read-next-card-footer {
  position: relative;
  margin: 15px 0 3px;
  text-align: center;
}

.read-next-card-footer a {
  color: #fff;
}

/* 7.5. Floating Header
/* ---------------------------------------------------------- */

.floating-header {
  visibility: hidden;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  height: 60px;
  border-bottom: rgba(0, 0, 0, 0.06) 1px solid;
  background: rgba(255, 255, 255, 0.95);
  transition: all 500ms cubic-bezier(0.19, 1, 0.22, 1);
  transform: translate3d(0, -120%, 0);
}

.floating-active {
  visibility: visible;
  transition: all 500ms cubic-bezier(0.22, 1, 0.27, 1);
  transform: translate3d(0, 0, 0);
}

.floating-header-logo {
  overflow: hidden;
  margin: 0 0 0 20px;
  font-size: 1.6rem;
  line-height: 1em;
  letter-spacing: -1px;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.floating-header-logo a {
  display: flex;
  align-items: center;
  color: var(--darkgrey);
  line-height: 1.1em;
  font-weight: 700;
}

.floating-header-logo a:hover {
  text-decoration: none;
}

.floating-header-logo img {
  max-height: 20px;
  margin: 0 10px 0 0;
}

.floating-header-divider {
  margin: 0 5px;
  line-height: 1em;
}

.floating-header-title {
  flex: 1;
  overflow: hidden;
  margin: 0;
  color: #2e2e2e;
  font-size: 1.6rem;
  line-height: 1.3em;
  font-weight: bold;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.floating-header-share {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-left: 2%;
  font-size: 1.3rem;
  line-height: 1;
}

.floating-header-share a {
  display: flex;
  justify-content: center;
  align-items: center;
}

.floating-header-share svg {
  width: auto;
  height: 16px;
  fill: #fff;
}

.floating-header-share-label {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  margin-right: 10px;
  color: rgba(0, 0, 0, 0.7);
  font-weight: 500;
}

.floating-header-share-label svg {
  width: 18px;
  height: 18px;
  margin: 0 5px 0 10px;
  stroke: rgba(0, 0, 0, 0.7);
  transform: rotate(90deg);
}

.floating-header-share-tw,
.floating-header-share-fb {
  display: block;
  align-items: center;
  width: 60px;
  height: 60px;
  color: #fff;
  line-height: 48px;
  text-align: center;
  transition: all 500ms cubic-bezier(0.19, 1, 0.22, 1);
}

.floating-header-share-tw {
  background: #33b1ff;
}

.floating-header-share-fb {
  background: #005e99;
}

.progress {
  position: absolute;
  right: 0;
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 2px;
  border: none;
  color: var(--blue);
  background: transparent;
  appearance: none;
}

.progress::-webkit-progress-bar {
  background-color: transparent;
}

.progress::-webkit-progress-value {
  background-color: var(--blue);
}

.progress::-moz-progress-bar {
  background-color: var(--blue);
}

.progress-container {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 2px;
  background-color: transparent;
}

.progress-bar {
  display: block;
  width: 50%;
  height: inherit;
  background-color: var(--blue);
}

@media (max-width: 900px) {
  .floating-header {
    height: 40px;
  }

  .floating-header-title,
  .floating-header-logo {
    font-size: 1.5rem;
  }

  .floating-header-share-tw,
  .floating-header-share-fb {
    width: 40px;
    height: 40px;
    line-height: 38px;
  }
}

@media (max-width: 800px) {
  .floating-header-logo {
    margin-left: 10px;
  }

  .floating-header-logo a {
    color: #2e2e2e;
  }

  .floating-header-title,
  .floating-header-divider {
    visibility: hidden;
  }
}

@media (max-width: 450px) {
  .floating-header-share-label {
    display: none;
  }
}

/* 8. Author Template
/* ---------------------------------------------------------- */

.site-header-content .author-profile-image {
  z-index: 10;
  flex-shrink: 0;
  width: 100px;
  height: 100px;
  margin: 0 0 20px;
  box-shadow: rgba(255, 255, 255, 0.1) 0 0 0 6px;
}

.site-header-content .author-bio {
  z-index: 10;
  flex-shrink: 0;
  max-width: 600px;
  margin: 5px 0 10px;
  font-size: 2rem;
  line-height: 1.3em;
  font-weight: 300;
  letter-spacing: 0.5px;
  opacity: 0.8;
}

.site-header-content .author-meta {
  z-index: 10;
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 0 10px;
  font-family: Georgia, serif;
  font-style: italic;
}

.site-header-content .author-location svg {
  height: 1.9rem;
  stroke: #fff;
}

.site-header-content .bull {
  display: inline-block;
  margin: 0 12px;
  opacity: 0.5;
}

.site-header-content .social-link:first-of-type {
  padding-left: 4px;
}

@media (max-width: 500px) {
  .site-header-content .author-bio {
    font-size: 1.8rem;
    line-height: 1.15em;
    letter-spacing: 0;
  }

  .author-location,
